<template>
  <div id="blog">
    <div class="section foreach">
      <div class="container">
        <h1 class="">{{post.title}}</h1>
        <div class="blog">
          <div class="content">
            <p class="author">By {{post.author.first_name}} {{post.author.last_name}}<br>{{post.published.substring(0, 10)}}</p>
            <p class="body" v-html="post.body"></p>
          </div>
          <div class="archive">
            <h1 class="title">Archive</h1>
            <ul>
              <li v-for="category in categories" :key="category.slug">
                <router-link :to="'/intelligence/'">
                  {{category.name}}
                </router-link>
              </li>
            </ul>
          </div>
        </div>
        <!-- <h2 class="cta">
          Hume.Works welcomes your thoughts and comments.
          <br>Together we can make a real difference in the way we live and work.
        </h2> -->
        <div class='comments'>
          <Disqus shortname='hume-works' :pageConfig="pageConfig" />
        </div>
      </div>  
    </div>
    <AddThis publicId="ra-5fe10eeb4e8f4f7b" />
  </div>
</template>

<script>
import Butter from 'buttercms';
const butter = Butter('2072fdd812123a2dd1a3f11b79c99b9b24c5faf5');
import { Disqus } from 'vue-disqus'
import AddThis from 'vue-simple-addthis-share'

export default {
  name: "BlogPost",
  data() {
    return {
      post: Object,
      categories: Object,
      pageConfig: {
        url: window.location.href,
        identifier: this.$route.params.slug
      }
    }
  },
  components: {
    Disqus,
    AddThis
  },
  created() {
    this.getPost()
    this.getCategories()
  },
  methods: {
    getPost () {
      butter.post.retrieve(this.$route.params.slug)
        .then((response) => {
          this.post = response.data.data
        })
    },
    getCategories() {
      butter.category.list()
      .then((response) => {
        this.categories = response.data.data
        // console.log(this.categories);
      })
    }
  }
}
</script>

